@import "styles/variables.scss";

.choose_size {
    font-variant-numeric: slashed-zero;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: -0.32px;
}

.sizes_container {
    position: absolute;
    bottom: 3px;
    left: 0;
    right: 0;
    z-index: 1;
    max-width: 100%;
    padding-top: 12px;
    padding-bottom: 16px;
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    height: auto;
    border-top: 1px solid #C9C9C2;
    border-bottom: 1px solid #C9C9C2;
    background: #F3F3EA;
    animation: fadeIn 0.3s ease-in-out forwards;
    -webkit-animation: fadeIn 0.3s ease-in-out forwards;
}

.sizes {
    position: relative;
    text-align: center;
    z-index: 2;
    display: flex;
    opacity: 0;
    gap: 4px;
    animation: fadeIn 0.4s ease-in-out forwards;

    div {
        display: flex;
        padding-top: 9px;
        padding-bottom: 9px;
        flex-direction: column;
        min-width: 32px;
        height: 14px;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 450;
        line-height: 100%;
        text-transform: uppercase;
        background: #F3F3EA;
        border: 1px solid var(--dark-gray-20, rgba(35, 35, 35, 0.20));

        &:hover {
            cursor: pointer;
            border: 1px solid black;
            transition: 0.5s;
        }
    }
}

.close {
    display: none;
}

.mobile_container {
    display: none;
}

@media #{$mobile} {
    .sizes_container {
        position: fixed !important;
        z-index: 999999;
        bottom: 0;
        left: 0;
        max-width: 100vw;
        overflow-x: hidden;
        right: 0;
        height: min-content;
        padding: 24px;
    }

    .close {
        position: absolute;
        display: block;
        top: 12px;
        right: 12px;
    }

    .sizes {
        position: block;

        div {
            &:active {
                cursor: pointer;
                border: 1px solid black;
                transition: 0.2s;
            }
        }
    }
}


@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slide-up {
    from {
        transform: translateY(100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}