@import "styles/variables.scss";

.isGradient {
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 15%, #F3F3EA 85%, rgba(0, 0, 0, 0) 100%);
}

.gradientLeft {
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 15%, #F3F3EA 100%);
}

.gradientRight {
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 0%, #F3F3EA 85%, rgba(0, 0, 0, 0) 100%);
}

.colorMobile {
    border-radius: 2px;
    width: 16px;
    height: 16px;
    box-sizing: border-box;
    max-width: 100%;
    flex-shrink: 0;
}

.isHoverMobile>div {
    display: flex;
    flex-wrap: nowrap;
    gap: 4px;
    max-width: 115px;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.isHover {
    display: flex;
}

.isHoverMobile>div::-webkit-scrollbar {
    display: none;
    -webkit-appearance: none;
    width: 0;
    height: 0;
}

.isHover>div {
    display: flex;
    flex-wrap: nowrap;
    row-gap: 5px;
    margin-right: 16px;
    max-width: 174px;
    min-width: fit-content;
    overflow: hidden;
}

.isHover>div:first-child>button {
    width: fit-content;
    white-space: nowrap;
}

.isHover>div:first-child>button:not(:last-child) {
    margin-right: 8px;
}

.isHover>div:last-child>button:not(:last-child) {
    margin-right: 4px;
}

.isHover>div:last-child>button {
    border-radius: 2px;
    width: 16px;
    height: 16px;
    box-sizing: border-box;
}

@media #{$tablet} {
    .isHover>div {
        max-width: auto;
    }
}