@import "styles/variables.scss";

.priceBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.priceBlock>p {
    white-space: nowrap;
}

.priceBlock>p:first-child {
    margin-right: 8px;
}

.priceBlock>p:last-child {
    letter-spacing: -0.02em;
    font-feature-settings: 'zero'
}

.oldPrice {
    text-decoration: line-through;
    font-size: 14px;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero'
}

@media #{$tablet} {
    .oldPrice {
        font-size: 11px;
    }

}

@media #{$mobile} {
    .priceBlock {
        align-items: center;
    }
}

@media #{$small} {
    .oldPrice {
        font-size: 10px;
    }

    .priceBlock>p:last-child {
        font-size: 11px;
    }
}