@import "styles/variables.scss";

.sale {
    position: absolute;
    top: 16px;
    left: 16px;
    background-color: $whiteColor;
    padding: 2px 8px;
    border-radius: 2px;
    z-index: 100;
}

@media #{$tablet} {
    .Product {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        height: 100%;
    }

    .sale {
        top: 8px;
    }

    .persentSale {
        top: 31px;
    }

    .sale,
    .persentSale {
        font-size: 12px;
        left: 8px;
    }

    .withThree {
        margin-bottom: 0;
    }

    .isHover>div {
        max-width: auto;
    }

    .actions {
        margin: 8px;
        height: 100%;
    }

    .oldPrice {
        font-size: 11px;
    }

    .actions>div:first-child {
        width: 80%;
    }

    .actions>div:first-child>a>p:first-child {
        height: 28px;
    }

    .icons {
        right: 8px;
    }
}
