@import "styles/variables.scss";

.Product {
    display: flex;
    flex-direction: column;
    width: 25%;
    box-sizing: border-box;
    border-right: 1px solid rgba(35, 35, 35, 0.3);
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    position: relative;
}

.Product>a {
    display: block;
    position: relative;
    z-index: 0;
}

.withThree {
    width: 33.3%;
    box-sizing: border-box;
    margin-bottom: 60px;
}

.Product>a {
    text-decoration: none;
}

.Product>span {
   position: relative;
}

.actions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 16px;
    gap: 8px;
    overflow: hidden;
    height: 100%;
}

.upper_actions {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.bottom_actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-height: 24px;
}

.isNovelties {
    overflow: hidden;
}

@media #{$tablet} {
    .Product {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    .withThree {
        margin-bottom: 0;
    }

    .isHover>div {
        max-width: auto;
    }

    .upper_actions { 
       overflow: hidden;
    }

    .actions {
        margin: 8px;
        height: 100%;
    }
}

@media #{$mobile} {
    .isNovelties {
        max-width: 90%;
        height: 100%;
    }
}