@import "styles/variables.scss";

.icons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.icons * {
    cursor: pointer;
}

.icons>:last-child {
    transform: translateY(0%);
}

.icons>:first-child {
    svg {
        fill: black;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media #{$tablet} {
    .icons {
        right: 8px;
    }
    .icons>:last-child {
        transform: translateY(0%);
    }
    
}