@import "styles/variables.scss";

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 53px;
    transform: translateX(-26%);
    z-index: 9999;
}

.tooltip {
    display: flex;
    padding: 10px 12px 12px 12px;
    width: 114px;
    height: 18px;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-variant-numeric: slashed-zero;
    gap: 10px;
    top: 0;
    left: 0;
    text-align: center;
    background: #000;
    color: white;
    white-space: nowrap;
}

.triangle {
    position: absolute;
    left: 109px;
    top: 40px;
    width: 16px;
    height: 8px;
}

@media #{$tablet} {
    .container {
        bottom: 42px;
        transform: translateX(-20.5%);
        z-index: 9999;
    }

    .tooltip {
        padding: 7px 12px 10px 12px;
        width: 98px;
        height: 15px;
        text-align: center;
        font-size: 11px;
        white-space: nowrap;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 125%;
        letter-spacing: -0.24px;
    }

    .triangle {
        position: absolute;
        left: 90px;
        top: 32px;
        width: 16px;
        height: 8px;
    }
}