@import "styles/variables.scss";

.images {
    width:  640px;
    padding-right: 20px;
}

.images > div {
    border: 1px solid rgba(35, 35, 35, 0.3);
    border-bottom: none;
}

.imagesScrollcontainer {
    width: 100%;
    overflow: hidden;
}

.imagesScrollBlock {
    width: fit-content;
    transition: 0.3s;
}

.imagesScrollBlock > img {
    display: inline-block;
    height: auto!important;
    position: relative!important;
    aspect-ratio: 375/542;
    background-image: url('/static/img/miniLoader.svg');
    background-size: 30%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
	object-fit: cover;
}

.mobileImages {
    position: relative;
    z-index: 100;
    //min-height: 555px;
}

.mobileImages > :first-child {
    //position: absolute!important;
    //top: 50%;
    //transform: translateY(-50%);
    z-index: 10;
}

.mobileImages > div:nth-child(4) {
    position: absolute;
}

.mobileImages > div:nth-child(4) > div {
    justify-content: space-between;
    width: calc(100vw + 2px);
    height: 130px;
    padding: 0;
    transform: translateY(1px) translateX(-1px);
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

.mobileImages > :last-child {
    //position: absolute!important;
    z-index: 10;
    //min-height: 555px;
}

.dotList {
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 40px;
    left: 12px;
}

.dotList > div {
    background-color: $darkBlue;
    margin-bottom: 6px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

